@import "../../../Common/Styles/sass/Mixins";

.title {
  margin-top: 100px;
  margin-bottom: 55px;

  h2 {
    @include text(36px, 700, 1.2);
    margin: 0 0 0 14px;
    position: relative;
    &::before {
      content: '';
      background-image: url("icons/icon3.png");
      display: block;
      height: 32px;
      left: -17px;
      top: -17px;
      position: absolute;
      width: 32px;
    }
  }

}



